import { useEffect, useState } from "react";
import "./Header.css";
import { Link } from "react-router-dom";
import { auth } from "../../firebase-config";
import { onAuthStateChanged, signOut} from "firebase/auth";
import { async } from "@firebase/util";
import Dropdown from 'react-bootstrap/Dropdown';



export default function Header() {
  const [position, setPosition] = useState(0);
  function onScroll() {
    setPosition(window.scrollY);
  }
  useEffect(() => {
    window.addEventListener("scroll", onScroll);
    return () => {
      window.removeEventListener("scroll", onScroll);
    };
  }, []);
  console.log((100 - position / 4) / 100);

  const [user, setUser] = useState({});
  const [loginStatus, setLoginStatus] = useState("Sign In");

  onAuthStateChanged(auth, (currentUser) => {
    if (currentUser == null) {
      setLoginStatus("Sign In");
    } else {
      setLoginStatus("Sign Out");
    }
    setUser(currentUser);
  });




  const registerManager = async () => {
    if (loginStatus.localeCompare("Sign Out") === 0) {
      await signOut(auth);
      console.log("HIHI");
      
    }
  };

  return (
    <div>
      <div className="custom-shape-divider-top-1657888838">
        <svg
          data-name="Layer 1"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1200 120"
          preserveAspectRatio="none"
        >
          <path
            d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z"
            opacity=".25"
            className="shape-fill"
          ></path>
          <path
            d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z"
            opacity=".5"
            className="shape-fill"
          ></path>
          <path
            d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z"
            className="shape-fill"
          ></path>
        </svg>
      </div>
      <header
        style={{
          transform: `scale(${
            1 + (0.1 - position / 1000)
          }) translateX(${-position}px)`,
          transition: "ease 0.5s",
        }}
      >
        <Link to="/">
          <img
            className="react_logo"
            alt="react_logo"
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/2300px-React-icon.svg.png"
          />
        </Link>

        <Link to="/webDev101" className="link">
          <div className="title">
            <h1 className="main-title">
              Webdev 101: <br />
              Intro to Webdev using React.js
            </h1>
            <h3 className="subtitle">by Kyochul Jang</h3>
          </div>
        </Link>
        {/* <h1>Position: {position}</h1> */}
        <div className="profile-wrapper">
          <Link to="/webDev101/auth">
            <button className="sign-in-btn" onClick={registerManager}>
              {loginStatus}
            </button>
          </Link>
          <h7 className="user-profile">{user?.email}</h7>
        </div>
      </header>
      <Dropdown>
      <Dropdown.Toggle variant="success" id="dropdown-basic">
        Dropdown Button
      </Dropdown.Toggle>

      <Dropdown.Menu>
        <Dropdown.Item href="webDev101/drop1">Action</Dropdown.Item>
        <Dropdown.Item href="#/action-2">Another action</Dropdown.Item>
        <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
      </Dropdown.Menu>
    </Dropdown>
    </div>
  );
}
